html {
  background-color: #F4F4F4;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 60%;
  margin: auto;
}

.header {
  display: flex;
  margin-top: 5em;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.intro {
  margin: 1rem auto;
  font-size: 1.8rem;
}

.button {
  margin: 0.5rem;
  width: 10rem;
}

.link {
  margin: 1rem;
}

.section {
  margin-bottom: 4rem;
}

.projects {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: stretch;
  flex-flow: wrap;
}

.project {
  width: calc(50% - 1.1rem);
  margin: 0.5rem;
  /* box-sizing: border-box; */
}

footer {
  /* background-color: #666666; */
  text-align: center;
}



@media only screen and (max-width: 1668px) {
  .App {
    width: 1000px;
  }
}

@media only screen and (max-width: 1250px) {
  .App {
    width: 80%;
  }
  .project {
    width: calc(100% - 1.1rem);
    margin: 0.5rem;
    /* box-sizing: border-box; */
  }
}

@media only screen and (max-width: 600px) {
  .App {
    width: 85%;
  }
  html {
    font-size: 12px;
  }
  .intro {
    margin: 1rem auto;
    font-size: 1.6rem;
  }
  .buttons {
    display: flex;
    flex-direction: column;
    width: 80%;
  }
  .button {
    width: 100%;
  }
  .uk-button {
    font-size: 1rem !important;
  }
  .uk-text-large {
    font-size: 1.3rem;
  }
  p {
    font-size: 1.2rem;
  }
}

@media only screen and (max-width: 400px) {
  /* html {
    font-size: 10px;
  } */
  .buttons {
    display: flex;
    flex-direction: column;
    width: 80%;
  }
  .button {
    width: 100%;
  }
}
